import React, { Component } from 'react';

import './homeHead.css';

import {
    Link,
} from 'react-router-dom';



class homeHead extends Component{
    render(){
        return(
            <div className="homeContainerDiv">
                <div className="myName"> <span>WU YUN</span></div>
                <div>
                    <div className="menuContainerDiv">
                        <div className="menuLeftPart">
                            <div className="menuText">
                                <Link to="/" className="home-page"></Link>
                            </div>
                            <div className="menuText">
                                <Link to="/live" className="my-live"></Link>
                            </div>
                        </div>
                        <div className="myPhoto">
                            <img src={require('common/js/home/img/myPhoto.png')} alt=""/>
                        </div>
                        <div className="menuRightPart">
                            <div className="menuText">
                                <Link to="/blog" className="bo-wen"></Link>
                            </div>
                            <div className="menuText" >
                                <Link to="/contract" className="contract-me"></Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default homeHead;


